.db-container{
    height:100vh;
    position:fixed;
    right:0px;
    width:calc(100vw - 16vw);
    overflow-y:auto;
    padding:10px;
    box-sizing: border-box;
}
.each-product{
    display: flex;
    flex-direction: column;
    align-items: center;
    max-width: 24%;
    min-width: 24%;
    padding:5px;
    box-sizing: border-box;
    flex:1;
    border: 1px solid gray;
    margin:2px;
    cursor: pointer;
    border-radius: 5px;
}
.img{
    width:100%;
}
.products-display{
    width:100%;
    display: flex;
    flex-wrap: wrap;
    /* align-items: center; */
    flex:1;
}
.prd-name{
    font-family: poppins-500;
    font-size:16px;
    color:rgb(42, 42, 42);
}
.img-div{
    display: flex;
    flex-grow:1;
    align-items: center;
    /* border: 2px solid green;s */
    transition: all 0.5s;
    overflow: hidden;
    cursor: pointer;
}
.img-div>img{
    transition: all 1s;
}
.img-div>img{
    transform:perspective(0px) translateZ(0px) scale(1);
}
.img-div:hover>img{
    /* border: 2px solid red; */
    transform:perspective(0px) translateZ(0px) scale(1.1);
}
.prdct-des{
   margin-top: 10px;
   text-align: center;
   height:100px;
}
.rating-div{
    /* border:2px solid red; */
    margin-top:5px;
    display: flex;
    justify-content: center;
}
.rating-div>.marked{
    color:rgb(255, 30, 30);
}
.marked>i,.unmarked>i{
    margin:2px;
}
.unmarked>.fa-star{
    /* border:2px solid red; */
    color:rgb(190, 190, 190)
}
.each-display{
position: fixed;
top:0px;
left: 0px;
display:flex;
justify-content: center;
align-items: center;
width: 100vw;
height:100vh;
background: rgba( 255, 255, 255, 0.25 );
box-shadow: 0 8px 32px 0 rgba( 31, 38, 135, 0.37 );
backdrop-filter: blur( 4px );
-webkit-backdrop-filter: blur( 4px );
border: 1px solid rgba( 255, 255, 255, 0.18 );
}
.each-prdct{
  /* width:600px; */
  /* height:600px; */
  max-width: 700px;
  background-color: white;
  padding:40px 40px 60px 40px;
  box-sizing: border-box;
  border-radius: 10px;
  border:1px solid rgb(204, 204, 204);
  position: relative;
  -webkit-user-select: none;
}

.e-prd-img-div{
    border:2px solid rgb(170, 170, 170);
    padding: 10px;
    width: fit-content;
    border-radius: 5px;
}
.e-prd-img-div>img{
    width:200px
}
.des-wrapper{
    display: flex;
    /* flex-direction: column;
    align-items: center; */
}
.e-des>.rating-div{
    width: fit-content;
    margin-left:-2px;
}
.e-des{
    font-family: poppins;
    color:rgb(63, 63, 63);
    margin-left:20px;
    /* border: 2px solid red; */
    position:relative;
   /* margin-top:15px; */
}
.price{
    margin-top: 20px;
    margin-left: 2px;
}

.btn-div{
    position:absolute;
    bottom:0px;
    /* border:2px solid red; */
    vertical-align: bottom;
    width: 100%;
}
.btn-div>button{
    width: 100%;
    font-family: poppins;
    border-radius:5px;
    border:none;
    outline: none;
    background-color: rgb(255, 88, 88);
    color: white;
    padding:5px 0px;
    cursor: pointer;
}
.btn-div>button:hover{
    background-color: rgb(255, 65, 65);
}
.stockin{
    color:green;
    margin-top: 20px;
}
.stockout{
    color:red;
    margin-top: 20px;
}
.exitDiv{
    display:flex;
    justify-content: flex-end;
    color: white;
    /* width:100%; */
    /* padding-right: 20px; */
    transition:all 1s;
    /* padding-top: 20px; */ 
    position:absolute;
    top:15px;
    right:15px;
}
.exit{
    cursor: pointer;
    -ms-transform-origin: 50% 50%;
    -ms-transform: rotate(0deg);
    transform: rotate(0deg);
    transform-origin: 50% 50%;
    transition:all 0.5s;
    display: flex;
    flex-direction: column;
   justify-content: center;
    width: 20px;
    height:20px;
}
.ediv_1{
  width:20px;
  height: 3px;
  background-color: rgb(255, 88, 88);
  transform: rotate(45deg);
}
.ediv_2{
    position:absolute;
    width:20px;
    height: 3px;
    background-color: rgb(255, 88, 88);
    transform: rotate(135deg);
  }
.book-text{
    font-family: poppins;
    margin-bottom: 20px;
